import React from 'react';
import { Text } from '@sitecore-jss/sitecore-jss-react';
import { COMPONENT_NAME } from './locators';
import Title from '../../../components/atoms/Title';
import Typography from '../../../components/atoms/Typography';
import Grid from '../../../components/molecules/Grid';
import useStyles from './style';
import Picture from '../../../components/atoms/Picture';
import { get } from 'lodash';
import Link from '@material-ui/core/Link';
import withErrorBoundary from '../../../utils/HOCs/withErrorBoundary';
import { Hidden } from '@material-ui/core';
import aspectRatios from '../../../utils/AspectRatios';
import { withNamespaces } from 'react-i18next';
import { withSitecoreContext } from '@sitecore-jss/sitecore-jss-react';
import { createDynamicLabels } from '../../../../utils/analytics';
import {
  getPageName,
  getComponentName,
} from '../../../utils/getPageComponentInfo';
import { analyticsData } from '../../../../constants/Analytics/analytics-data';
import RichText from '../../../components/atoms/RichText';

const UsefulAppItem = props => {
  const {
    title,
    description,
    image,
    mobileImage,
    googlePlayLink,
    appStoreLink,
    googlePlayDownloadIcon,
    appStoreDownloadIcon,
  } = get(props, 'appData.fields', {});
  const classes = props.classes;

  const imageWithFallBackFeature = {
    value: {
      src: get(
        image,
        'value.src',
        'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAQAAAC1HAwCAAAAC0lEQVR42mP8Xw8AAoMBgDTD2qgAAAAASUVORK5CYII='
      ),
      alt: get(image, 'value.name', ''),
      '1x1': {
        large: get(
          image,
          'value.src',
          'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAQAAAC1HAwCAAAAC0lEQVR42mP8Xw8AAoMBgDTD2qgAAAAASUVORK5CYII='
        ),
      },
    },
  };

  const mobileImageWithFallBackFeature = {
    value: {
      src: get(
        image,
        'value.src',
        'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAQAAAC1HAwCAAAAC0lEQVR42mP8Xw8AAoMBgDTD2qgAAAAASUVORK5CYII='
      ),
      alt: get(mobileImage, 'value.name', ''),
      '1x1': {
        large: get(
          image,
          'value.src',
          'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAQAAAC1HAwCAAAAC0lEQVR42mP8Xw8AAoMBgDTD2qgAAAAASUVORK5CYII='
        ),
        small: get(
          mobileImage,
          'value.src',
          'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAQAAAC1HAwCAAAAC0lEQVR42mP8Xw8AAoMBgDTD2qgAAAAASUVORK5CYII='
        ),
      },
    },
  };

  return (
    <div className={classes.appItem}>
      <div className={`${classes.appImage} imageSection`}>
        <Hidden mdDown implementation="css">
          <Picture
            isParalax={false}
            media={{
              image: imageWithFallBackFeature,
              aspectRatio: aspectRatios['1x1'],
            }}
            alternateAltText={title && title.value}
          />
        </Hidden>
        <Hidden lgUp implementation="css">
          <Picture
            isParalax={false}
            media={{
              image: mobileImageWithFallBackFeature,
              aspectRatio: aspectRatios['1x1'],
            }}
            alternateAltText={title && title.value}
          />
        </Hidden>
      </div>
      <div>
        <Grid container className={classes.appContent}>
          <Grid item xs={12} lg={10}>
            {get(title, 'value') && (
              <Typography variant="h5" component="h3">
                <Text field={title} />
              </Typography>
            )}
          </Grid>
          <Grid item xs={12} lg={10} className={classes.appDescription}>
            {get(description, 'value') && (
              <Typography variant="body1" component="div">
                <RichText innerHTML={description} />
              </Typography>
            )}
          </Grid>
          <Grid item xs={12} lg={10}>
            <div className={classes.downloadApp}>
              {get(googlePlayLink, 'value.href') && (
                <Link
                  data-id={analyticsData.dataIds.pageBodyNavAppClick}
                  data-link={createDynamicLabels([
                    props.pageName,
                    props.componentName,
                    `${analyticsData.dataLinksText.downloadapp} - ${get(
                      googlePlayLink,
                      'value.text',
                      ''
                    )}`,
                  ])}
                  aria-label={get(googlePlayLink, 'value.href')}
                  href={get(googlePlayLink, 'value.href')}
                  className={classes.downloadAppLogo}
                  target="_blank"
                >
                  <Picture
                    media={{
                      image: get(googlePlayDownloadIcon, 'fields.icon'),
                      renditionNeeded: false,
                      disableLazyLoad: true,
                      fallbackBackgroundNeeded: false,
                    }}
                    alternateAltText={title && title.value}
                    isParalax={false}
                  />
                </Link>
              )}
              {get(appStoreLink, 'value.href') && (
                <Link
                  data-id={analyticsData.dataIds.pageBodyNavAppClick}
                  data-link={createDynamicLabels([
                    props.pageName,
                    props.componentName,
                    `${analyticsData.dataLinksText.downloadapp} - ${get(
                      appStoreLink,
                      'value.text',
                      ''
                    )}`,
                  ])}
                  aria-label={get(appStoreLink, 'value.href')}
                  href={get(appStoreLink, 'value.href')}
                  className={classes.downloadAppLogo}
                  target="_blank"
                >
                  <Picture
                    media={{
                      image: get(appStoreDownloadIcon, 'fields.icon'),
                      renditionNeeded: false,
                      disableLazyLoad: true,
                      fallbackBackgroundNeeded: false,
                    }}
                    alternateAltText={title && title.value}
                    isParalax={false}
                  />
                </Link>
              )}
            </div>
          </Grid>
        </Grid>
      </div>
    </div>
  );
};

const UsefulAppsSection = props => {
  const { segmentName, title, usefulAppsList } = get(
    props,
    'sectionData.fields',
    {}
  );
  const classes = props.classes;
  return (
    <div className={classes.appsSection}>
      {segmentName && <Title text={segmentName} className={classes.heading} />}
      {title && (
        <Typography
          variant="h4"
          className={classes.appsSectionHeading}
          component="h2"
          noWrap
        >
          <Text field={title} />
        </Typography>
      )}
      {usefulAppsList &&
        usefulAppsList.map((appData, index) => (
          <UsefulAppItem
            key={`${get(appData, 'fields.title.value')}-${get(
              appData,
              'fields.description.value'
            )}-${index}`}
            {...{ appData, classes }}
            pageName={props.pageName}
            componentName={props.componentName}
          />
        ))}
    </div>
  );
};

const UsefulApps = props => {
  const pageName = getPageName(props);
  const componentName = getComponentName(props);
  const { usefulAppSectionList } = get(props, 'fields', {});
  const classes = useStyles();

  return (
    <Grid className={classes.root} data-locator={COMPONENT_NAME}>
      {usefulAppSectionList &&
        usefulAppSectionList.map((sectionData, index) => (
          <UsefulAppsSection
            key={`${get(sectionData, 'fields.segmentName.value')}-${get(
              sectionData,
              'fields.title.value'
            )}-${index}`}
            {...{
              sectionData,
              classes,
            }}
            pageName={pageName}
            componentName={componentName}
          />
        ))}
    </Grid>
  );
};

export default withErrorBoundary(
  withSitecoreContext()(withNamespaces()(UsefulApps))
);
